import { AxiosInstance } from "../../../shared/api"
import { GetTicketsPayload } from "../types"

export const getTicketList = async (payload: GetTicketsPayload) => {
  return await AxiosInstance.get(`/api/tickets`, {
    params: payload,
  })
}

export const approoveTicket = async (id: string) => {
  return await AxiosInstance.post(`/api/tickets/${id}/approve`, {})
    .then(response => {
      return response.data
    })
    .catch(error => {
      console.error("Error fetching tickets:", error)
      return error
    })
}
export const declineTicket = async (id: string) => {
  return await AxiosInstance.post(`/api/tickets/${id}/decline`, {})
    .then(response => {
      return response.data
    })
    .catch(error => {
      console.error("Error fetching tickets:", error)
      return error
    })
}
