"use client"
import { Table, Button, Image, Flex, Tooltip } from "antd"
import { getTicketList } from "../api"
import { Ticket } from "../../../shared/entities/Ticket/Ticket"
import React, { useState } from "react"
import { AppTag } from "../../../shared/ui/AppTag/ui"
import { StatusEnum } from "../../../shared/entities/Ticket/StatusEnum"
import { Prove } from "../../../shared/entities/Prove/Prove"
import { CheckCircleOutlined, DeleteOutlined } from "@ant-design/icons"
import { FilterForm } from "../../../features/FilterForm/ui"

export default async function TicketList({
  searchParams,
}: {
  searchParams: { userName?: string; startDate?: string; endDate?: string; limit?: string; offset?: string }
}) {
  const { userName, startDate, endDate, limit, offset } = searchParams || {}
  const [data, setData] = useState<Ticket[]>([])

  getTicketList({
    userName,
    startDate,
    endDate,
    limit: limit ? Number(limit) : 10,
    offset: offset ? Number(offset) : 0,
  })
    .then(response => {
      setData(response.data)
    })
    .catch(error => {
      console.error("Error fetching tickets:", error)
    })

  // Example columns, adjust as needed
  const columns = [
    { title: "Студент", dataIndex: "user.name", key: "userName" },
    { title: "По дату", dataIndex: "startDate", key: "startDate" },
    { title: "с даты", dataIndex: "endDate", key: "endDate" },
    {
      title: "Статус",
      dataIndex: "endDate",
      key: "endDate",
      renderItem: (text: string) => {
        return (
          <AppTag
            variant={text === StatusEnum.PENDING ? "default" : text === StatusEnum.APPROVED ? "success" : "danger"}
          >
            {text}
          </AppTag>
        )
      },
    },
    {
      title: "Причина",
      dataIndex: "endDate",
      key: "endDate",
      renderItem: (text: string) => {
        return <AppTag variant='info'>{text}</AppTag>
      },
    },
    {
      title: "Подтверждения",
      dataIndex: "prooves",
      key: "prooves",
      renderItem: (prooves: Prove[]) => {
        return (
          <>
            {prooves.map(item => {
              return (
                <Image
                  preview
                  style={{ width: 50, height: 50 }}
                  key={item.id}
                  src={item.path}
                  alt='empty'
                />
              )
            })}
          </>
        )
      },
    },
    {
      title: "Действия",
      dataIndex: "actions",
      key: "actions",
      renderItem: ({ id }: Ticket) => {
        return (
          <Flex
            justify='center'
            gap={"12px"}
          >
            <Tooltip title='Быстрое подтверждение'>
              <Button
                onClick={() => console.log(`Approve ticket with id: ${id}`)}
                type='primary'
                size='small'
              >
                <CheckCircleOutlined />
              </Button>
            </Tooltip>
            <Tooltip title='Быстрое отклонение'>
              <Button
                onClick={() => console.log(`Approve ticket with id: ${id}`)}
                color='danger'
                type='primary'
                size='small'
              >
                <DeleteOutlined />
              </Button>
            </Tooltip>
          </Flex>
        )
      },
    },
  ]

  return (
    <div>
      <FilterForm
        userName={userName}
        startDate={startDate}
        endDate={endDate}
        limit={limit ? Number(limit) : 10}
        offset={offset ? Number(offset) : 0}
      />
      <Table
        columns={columns}
        dataSource={result}
        rowKey='id'
        pagination={false}
      />
    </div>
  )
}
